<template>
	<view class="register">
		<view class="mititle">
			个人中心
		</view>
		<u-row class="header">
			<u-col span="3" style="text-align: center;">
				<u-avatar src="/static/img/banner.png" size="100"></u-avatar>
			</u-col>
			<u-col span="9">
				<span>您好，{{zxlist.nickName}}</span>
				<p>{{zxlist.updateTime}}</p>
			</u-col>
		</u-row>
		<view class="formBox">
			<u-cell-group class="tsbox">
				<u-cell-item title="我的上报记录" @click="topage(0)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="/static/img/my_icon6.png"
						mode="widthFix"></img>
				</u-cell-item>
		<!-- 		<u-cell-item title="关于我们">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="/static/img/my_icon7.png"
						mode="widthFix"></img>
				</u-cell-item> -->
				<u-cell-item title="设置" @click="topage(8)">
					<img style="width: 24rpx;margin-right: 12rpx;" slot="icon" src="/static/img/my_icon8.png"
						mode="widthFix"></img>
				</u-cell-item>
			</u-cell-group>
		</view>
		<u-button @click="exit">退出登录</u-button>
			<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		getUserInfo
	} from '../../api/index.js'
	export default {
		data() {
			return {
				zxlist: {},
			}
		},
		onLoad() {
			getUserInfo().then(res => {
				if (res.code == 200) {
					this.zxlist = res.user;
				}
			})
		},
		methods: {
			exit(){
				/*enterAbout({
					id: this.zxlist.id
				}).then(res => {
					if (res.code == 200) {*/
						this.$refs.uToast.show({
							title: '退出成功'
						});
						uni.setStorageSync('token', '');
						uni.navigateTo({
							url: '../login/login'
						})
					//}
				//});
			},
			topage(index) {
				let link = '';
				if (index == 0) {
					link = '../report/list';
				} else if (index == 1) {
					link = '../report/report';
				} else if (index == 2) {
					link = '../news/list';
				}else if (index == 8) {
					link = '../my/set';
				}
				uni.navigateTo({
					url: link
				})
			},
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}

	.register /deep/ .u-btn {
		border: none;
		border-radius: 0;
		margin-top: 20rpx;
		position: initial;
	}

	.header {
		padding: 65rpx 0 67rpx 20rpx;
		background-color: #1F61BE;
		color: #fff;
	}

	.header span {
		font-size: 28rpx;
		font-weight: bold;
	}

	.header p {
		margin-top: 12rpx;
		font-size: 24rpx;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		background-color: #1F61BE;
		text-align: center;
		font-size: 32rpx;
		font-weight: bold;
		color: #fff;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		position: relative;
	}

	.tsbox /deep/ .u-cell_title {
		font-size: 24rpx;
		color: #333;
	}

	.tsbox /deep/ .u-cell__value {
		font-size: 24rpx;
		color: #4490FB;
	}
</style>