<template>
	<view>

		<ptindex v-if="tabIndex==0" :mytype="mytype"></ptindex>
		<ptmy v-if="tabIndex==1"></ptmy>
		
		<hans-tabber :list="tabbarList" :num="noread_num" @tabChange="tabChange" style="position:fixed;bottom:0;width:100%;left:0;right:0;"></hans-tabber>
		

	</view>
</template>
 
<script>
	import ptindex from './pt_index'
	import ptmy from './pt_my'
	import hansTabber from './hans-tabbar'

	export default {
		components:{
			ptindex,
			ptmy,
			hansTabber
		},
		data() {
			return {
				mytype:'',
				tabbarList: [{
						iconPath: "/static/img/home.png",
						selectedIconPath: "/static/img/homel.png",
						text: "首页"
					},
					{
						iconPath: "/static/img/my.png",
						selectedIconPath: "/static/img/myl.png",
						text: "我的",
					}
				],
				tabIndex:0,
				noread_num:0
			};
		},

		onLoad() {
			this.mytype=this.$route.query.type;
		},
		methods:{
			tabChange(index) {
				console.log(index)
				this.tabIndex=index;
			}
		}
	}
</script>

